








import { Vue, Component } from "vue-property-decorator";
import { isPC, autoResize } from "@/utils/index";

@Component({})
export default class Main extends Vue {
  mounted() {
    autoResize();
    this.$nextTick(() => {
      this.setRem();
    });
  }

  setRem() {
    if (!isPC()) {
      // 非PC
      document.getElementsByTagName("html")[0].style.fontSize =
        "13.33333333333333vmin";
    }
  }
}
